// General structure

.col-lg-12 {
	.alignwide {
		width: 100vw;
		max-width: 1500px;
		margin: 1.5em 0 1.5em 50%;
		transform: translateX(-50%);
	}

	.alignfull {
		width: 100vw;
		max-width: 100vw;
		margin: 1.5em 0 1.5em 50%;
		transform: translateX(-50%);
	}
}
// if has sidebar then ignore alignfull and alignwide
.col-lg-8, .card {
	.alignfull,
	.alignwide {
		width: 100%;
		margin-left: 0px;
		margin-right: 0px;
		transform: translateX(0%);
	}
}

@media screen and (min-width: 768px) {
  .entry-content > * {
    padding-left: 0px;
    padding-right: 0px;
  }
}

// Columns

.wp-block-columns {
  padding-right: 1rem;
  padding-left: 1rem;
}

// Block separator

.wp-block-separator {
  margin: 3em auto;
  padding: 0;
}
